import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

const PrivacyPage = () => (
  <Layout>
    <SEO title="Privacy Policy" />
    <h1>Our Privacy Policy</h1>
    <p>
      Well this is easy. I'm a student at UW and between wanting to build this
      app within 2 weeks and wanting to make the app as easy to use as possible,
      we made it so the app is completely offline.
      <br />
      <br />
      This means we collect ZERO user data, not even for advertising (not that
      there are any ads on the app at the moment) and we do not store any of
      your list data on a server. All the data is encrypted on your end user
      device and we do not sell your data.
    </p>
  </Layout>
)

export default PrivacyPage
